.intro{
    background-color: lightcyan;
    display: flex;
    

    .left{
        flex:0.5;
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            float: left;
            img{
                height: 90%;
            }
        }

    }
    .right{
        flex:0.5;


        .wrapper{
            width:100%;
            height:100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;


            h1{
                font-size: 60px;
                margin: 10px 0;
            }
            h2{
                font-size: 35px;
                
            }
            h3{
                font-size: 30px;
                
                span{
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor{
                  animation: blink 1s infinite;  
                }
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }

            }
        }
        
        a{
            position: absolute;
            bottom: 10px;
            left: 50%;

            img {
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}
