.contact{
    background-color: white;
    display: flex;


    .left{
        flex: 1;
    
        img{
            height: 80%;
            }
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;


        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;
           }

           textarea{
               width: 300px;
               height: 200px;
               font-size: 14px;
           }

           button{
               width: 150px;
               height: 30px;
               color: white;
               background-color: crimson;
               border-radius: 10px;
               cursor: pointer;
           }
        }


    }
}